import { Component, Prop, Event, EventEmitter, State, h, Host, Listen } from '@stencil/core';

export interface BhProduct {
  id: string;
  name: string;
  description: string;
  logo: string;
  current?: boolean;
}

export interface AnchorPosition {
  top: number;
  left: number;
}

@Component({
  tag: 'bh-product-switcher',
  styleUrl: 'bh-product-switcher.css',
  shadow: true,
})
export class BhProductSwitcher {
  /**
   * Whether the product switcher is open
   */
  @Prop() isOpen: boolean = false;

  /**
   * Position to anchor the popover
   */
  @Prop() anchorPosition: AnchorPosition = { top: 0, left: 0 };

  /**
   * Array of products to display
   */
  @Prop() products: BhProduct[] = [
    {
      id: 'alice',
      name: 'Alice',
      description: 'Housekeeping & Guest Services',
      logo: './assets/logos/AL.svg',
    },
    {
      id: 'hotel-effectiveness',
      name: 'Hotel Effectiveness',
      description: 'Labor planning',
      logo: './assets/logos/HE.svg',
    },
    {
      id: 'profit-sword',
      name: 'ProfitSword',
      description: 'B.I and Finance',
      logo: './assets/logos/PS.svg',
    },
    {
      id: 'transcendent',
      name: 'Transcendent',
      description: 'Asset Management & CapEx',
      logo: './assets/logos/TR.svg',
    },
  ];

  /**
   * Event emitted when the switcher should close
   */
  @Event() bhClose!: EventEmitter<void>;

  /**
   * Event emitted when a product is selected
   */
  @Event() bhProductSelect!: EventEmitter<BhProduct>;

  @State() isClosing: boolean = false;

  private containerRef?: HTMLDivElement;

  @Listen('click', { target: 'document' })
  handleDocumentClick(event: MouseEvent) {
    const target = event.target as HTMLElement;
    if (
      this.isOpen &&
      this.containerRef &&
      !this.containerRef.contains(target) &&
      !target.closest('.logo-active')
    ) {
      this.handleClose();
    }
  }

  private handleClose = () => {
    this.isClosing = true;
    setTimeout(() => {
      this.bhClose.emit();
      this.isClosing = false;
    }, 200);
  };

  private handleProductClick = (product: BhProduct) => {
    this.bhProductSelect.emit(product);
    this.handleClose();
  };

  render() {
    if (!this.isOpen) return null;

    const containerClasses = {
      'product-switcher-container': true,
      'closing': this.isClosing,
    };

    return (
      <Host>
        {/* Backdrop */}
        <div class="product-switcher-backdrop" onClick={this.handleClose}></div>

        {/* Product Switcher Container */}
        <div
          ref={(el) => this.containerRef = el}
          class={containerClasses}
          style={{
            top: `${this.anchorPosition.top}px`,
            left: `${this.anchorPosition.left}px`,
          }}
        >
          <div class="product-switcher-popover">
            {this.products.map((product) => {
              const itemClasses = {
                'product-item': true,
                'current': product.current || false,
              };

              return (
                <div
                  key={product.id}
                  class={itemClasses}
                  onClick={() => this.handleProductClick(product)}
                >
                  <div class="product-icon-container">
                    <img
                      src={product.logo}
                      alt={`${product.name} logo`}
                      class="product-icon"
                    />
                  </div>

                  <div class="product-info">
                    <div class="product-name">{product.name}</div>
                    <div class="product-description">{product.description}</div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </Host>
    );
  }
}
